import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {startWith} from 'rxjs/operators/startWith';
import {map} from 'rxjs/operators/map';

import {Component, Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatDatepickerInputEvent} from '@angular/material';
import { ExampleDialogComponent } from './example-dialog/example-dialog.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  currentTheme = "indigo"
  title = 'app';

  myControl: FormControl = new FormControl();
  filteredOptions: Observable<string[]>;
  options: string[] = [
    "saran",
    "lara",
    "sachin"
  ];

  animal: string;
  name: string;

  constructor(public dialog: MatDialog){
    document.body.classList.add(`${this.currentTheme}-theme`);
    this.filteredOptions = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(val => this.filter(val))
      );      
  }

  openDialog(): void {
    let dialogRef = this.dialog.open(ExampleDialogComponent, {
      width: '250px',
      data: { name: this.name, animal: this.animal }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.animal = result;
    });
  }

  

  filter(val: string): string[] {
    return this.options.filter(option =>
      option.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }

  setTheme(color){
    document.body.classList.remove(`${this.currentTheme}-theme`)
    this.currentTheme = color;
    document.body.classList.add(`${this.currentTheme}-theme`)
  }


  dateChoosen(type: string, event: MatDatepickerInputEvent<Date>) {
    alert(event.value.getTimezoneOffset());
  }
}


