import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Person } from './model/student';
import { Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable()
export class PersonService {

  constructor(
    protected http: HttpClient
  ) { }

  addStudent (student: Person): Observable<Person> {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };
    return this.http.post<Person>("/api/people/", student, httpOptions);
  }

  getStudents(): Observable<PersonBObj[]> {
    return this.http.get<PersonBObj[]>("/api/people");
 }
}

export class PersonBObj{
  firstName: string;
}


