import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import {MatButtonModule, MatCheckboxModule} from '@angular/material';
import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { MyOwnCustomMaterialModule } from './MyOwnCustomMaterialModule';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ExampleDialogComponent } from './example-dialog/example-dialog.component';
import { FormTestComponent } from './form-test/form-test.component';
import { ForbiddenValidatorDirective } from './form-test/forbidden-name.directive';
import { LayoutTestComponent } from './layout-test/layout-test.component';
import { StudentFormComponent } from './student-form/student-form.component';
import { PersonService } from './person.service';
import { HttpClientModule } from '@angular/common/http';


@NgModule({
  declarations: [
    AppComponent,
    ExampleDialogComponent,
    FormTestComponent,
    ForbiddenValidatorDirective,
    LayoutTestComponent,
    StudentFormComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MyOwnCustomMaterialModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  providers: [ForbiddenValidatorDirective, PersonService],
  entryComponents: [
    ExampleDialogComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



